@import url('https://fonts.cdnfonts.com/css/dm-serif-text');
@import url('https://fonts.cdnfonts.com/css/gloock-2');
@import url('https://fonts.cdnfonts.com/css/anton');
@import url('https://fonts.cdnfonts.com/css/perandory');
@import url('https://fonts.cdnfonts.com/css/crimson-pro');

.landing-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-color: white;
    /* overflow: hidden; */
}

.ball {
    width: 50px;
    height: 50px;
    background-color: #82A0F4;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    animation: bounce 2s ease-in-out forwards;
}

@keyframes bounce {
    0% {
        top: 0;
    }

    45% {
        transform: translateX(-50%) scaleX(1);
    }

    50% {
        top: calc(100vh - 45px);
        transform: translateX(-50%) scaleX(1.2) scaleY(0.8);
    }

    70% {
        top: calc(100vh - 150px);
        transform: translateX(-50%) scaleX(1);
    }

    100% {
        top: calc(100vh - 45px);
    }
}

.background {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    clip-path: circle(0% at 50% 100%);
    animation: backgroundExpand 1s ease-in 1.8s forwards;
}

@keyframes backgroundExpand {
    0% {
        clip-path: circle(0% at 50% 100%);
    }

    100% {
        clip-path: circle(150% at 50% 100%);
    }
}

.top-stretch {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    gap: 60px;
    position: relative;
    text-align: center;
    margin-top: 10px;
}

.top-stretch::before,
.top-stretch::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: #353535;
    left: 50%;
    transform: translateX(33%);
}

.top-stretch::before {
    /* first line */
    height: 1.5px;
    top: 10px;
    /* Adjust as needed */
    left: 0;
}

.top-stretch::after {
    /* second line */
    height: 3px;
    bottom: -0px;
    /* Adjust as needed */
    left: 0;
}

.left-stretch,
.right-stretch {
    padding: 15px 25px;
    border: 2px solid #353535;
    margin-top: 20px;
    font-style: italic;
}

.left-stretch h4,
.right-stretch h4 {
    font-weight: 100;
    text-transform: uppercase;
    font-family: 'Gloock', sans-serif;
    color: #666666;
}

.center-stretch-title {
    font-size: 2.5em;
    font-family: 'Gloock', sans-serif;
    letter-spacing: 7px;
}

.center-stretch-content p {
    font-size: 1em;
    font-family: 'Gloock', sans-serif;
}

.headline-container::before,
.headline-container::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: #353535;
    left: 50%;
    transform: translateX(33%);
}

.headline-container::before {
    /* third line */
    height: 8px;
    left: 0;
    margin-top: 5px;
    /* top: 22.5%; */
}

.headline-container::after {
    /* fourth line */
    height: 8px;
    left: 0;
    /* bottom: 0; */
}

.headline-container {
    text-align: center;
    width: 60%;
    margin: 0 auto;
    text-transform: uppercase;
    margin-top: 0px;
}

.headline-container h1 {
    font-family: 'Anton', sans-serif;
    font-size: 130px;
}

.headline-container h2 {
    margin-top: -40px;
    font-size: 100px;
    font-family: 'Perandory', sans-serif;
}

.content-container {
    display: grid;
    grid-template-columns: 14% 29.5% 16.5%;
    justify-content: center;
    margin-top: 40px;
}

.content-container::before,
.content-container::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: #353535;
    left: 50%;
    transform: translateX(33%);
}

.content-container::before {
    /* fifth line */
    height: 3px;
    left: 0;
    margin-top: -25px;
}

.content-container::after {
    /* sixth line */
    height: 1.5px;
    left: 0;
    margin-top: 500px;
}

.left-content-column h5 {
    font-family: 'Gloock', sans-serif;
    letter-spacing: 2px;
}

.left-content-column p {
    font-family: 'Crimson Pro', sans-serif;
    margin-top: 5px;
    text-align: justify;
}

.center-content-column {
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-content-column img {
    width: 100%;
    max-height: 400px;
    object-fit: contain;
}

.right-content-column h5 {
    font-family: 'Gloock', sans-serif;
    letter-spacing: 2px;
}

.right-content-column p {
    font-family: 'Crimson Pro', sans-serif;
    margin-top: 5px;
    text-align: justify;
}

.bottom-content {
    width: 60%;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 50px;
}

.bottom-content p {
    text-align: justify;
    font-family: 'Crimson Pro', sans-serif;
}

/* responsiveness for small devices like phones */

@media screen and (max-width: 600px) {

    .ball {
        animation: none;
        /* removes animation */
        display: none;
        /* removes ball display from top of screen*/
    }

    .background {
        animation: none;
        /* removes animation */
        display: contents;
        /* still displays content without animation */
    }

    .top-stretch::after {
        bottom: 10px;
    }

    .headline-container::before {
        margin-top: -15px;
    }

    .headline-container::after {
        margin-top: 0px;
    }

    .content-container::before {
        margin-top: -1120px;
    }

    .content-container::after {
        margin-bottom: -1200px;
    }

    .left-stretch,
    .right-stretch {
        display: none;
    }

    .center-stretch-title {
        font-size: 15px;
    }

    .headline-container {
        margin-top: 10px;
    }

    .headline-container h1 {
        font-size: 50px;
        line-height: 4rem;
    }

    .headline-container h2 {
        font-size: 40px;
        margin-top: 10px;
    }

    .content-container {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        margin-top: 30px;
        width: 60%;
    }

    .center-content-column {
        margin-top: 10px;
    }

    .right-content-column {
        margin-top: 20px;
    }
}

/* responsiveness for medium devices like tablets */

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .top-stretch {
        gap: 30px;
    }

    .headline-container {
        margin-top: 15px;
    }

    .headline-container::before {
        margin-top: -10px;
    }

    .headline-container h1 {
        font-size: 100px;
    }

    .headline-container h2 {
        margin-top: -30px;
    }

    .content-container {
        display: flex;
        flex-direction: column;
        width: 60%;
        margin: 0 auto;
        margin-top: 40px;
    }

    .content-container::before {
        margin-top: -760px;
    }

    .content-container::after {
        margin-top: 1000px;
    }
}